.completedQuestions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  width: 350px;
  margin: 1em;
  border-bottom: 1px solid grey;

}

.completedQuestions > div {
  width: 10px;
  height: 10px;
  display: inline;
  border-radius: 100px;
  margin: 10px;
  -webkit-box-shadow: 0px 2px 2px 0px rgba(0,0,0,.5);
          box-shadow: 0px 2px 2px 0px rgba(0,0,0,.5)
}

.completedQuestions .correct {
  -webkit-transition: background-color 1s;
  -o-transition: background-color 1s;
  transition: background-color 1s;
  background-color: #75D56B;
  width: 100%;
  height: 100%;
  border-radius: 100px;
}

.completedQuestions .wrong {
  -webkit-transition: background-color 1s;
  -o-transition: background-color 1s;
  transition: background-color 1s;
  border-radius: 100px;
  background-color: #FB7E84;
  height: 100%;
}

.completedQuestions .notAnswered {
  -webkit-transition: background-color 1s;
  -o-transition: background-color 1s;
  transition: background-color 1s;
  background-color: lightgrey;
  height: 100%;
  border-radius: 100px;
}



