nav {
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

nav button {
    padding: 15px 30px;
    outline: none;
    border: none;
    background: none;
    color: white;
    border-top: 3px solid transparent;
    font-size: 18px;
    -webkit-box-shadow: none;
            box-shadow: none;
}

nav button:hover {
    cursor: pointer;
}

nav button.active {
    border-top: 3px solid #ad8cff;
}



